<template>
	<view class="box">
		<th-autograph :isDownload=false @submit="next" canvas-id="autograph">
		</th-autograph>
	</view>
</template>

<script>
	import thAutograph from "../../uni_modules/th-autograph/components/th-autograph/th-autograph.vue"
	export default {
		components: {
			thAutograph
		},
		data() {
			return {
				stages: '1'
			}
		},
		onLoad() {
			uni.hideTabBar();
		},
		methods: {
			next() {
				if (this.stages == '1') {
					uni.showModal({
						title: '提示',
						content: '确定要进入下一阶段吗？',
						success: function(res) {
							if (res.confirm) {
								uni.redirectTo({
									url: '/pages/watch_data/watch_data'
								})
							} else if (res.cancel) {}
						}
					});
				} else {
					uni.showModal({
						title: '提示',
						content: '确定要结束绘画吗？',
						success: function(res) {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/home/home'
								})
							} else if (res.cancel) {}
						}
					});

				}
			}
		},
		onLoad(options) {
			this.stages = options.stage;
			uni.hideNavigationBarLoading();
		}
	}
</script>

<style>
	@import './new_draw.css'
</style>